/*
Rich Text Editor

Styleguide 1.0
*/

/*
Hyperlink

markup:
<div class="c-rte">
  <a href="http://www.afr.rw" class="c-rte__link">www.afr.rw</a>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 1.1
*/

.c-rte a,
.c-rte__link,
.c-rte__accordion a {
  @include default($color: $black);

  transition: all 0.4s $cubic-transition;
  font-weight: bold;
  border-bottom: 2px solid $light-blue;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $light-blue;
    border-bottom: 2px solid $black;
  }
}

/*
Heading

markup:
<div class="c-rte">
  <h1>Header cipsum dolor sit amet duo erat sea</h1>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 1.2
*/

.c-rte h1 {
  font-weight: bold;

  &:before {
    content: " ";
    display: block;
    border-bottom: 5px solid $red;
    width: 20%;
  }
}

.c-rte__text-spacing {
  margin-left: 20%;
}

/*
Quote Offset

markup:
<div class="c-rte__offset-quote">
  <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.<span>Lorem Ipsum</span></p>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 1.3
*/

blockquote,
.c-rte__offset-quote,
.c-rte__accordion blockquote {
  width: 40%;
  float: right;
  padding: 20px;
  background: $light-gray;
  border-bottom-right-radius: 150px;
  padding-right: 10%;
  margin-left: 30px;
  margin-bottom: 30px;

  @include grid-media($lg) {
    margin-left: 60px;
    margin-bottom: 60px;
  }


  p {
    font-size: 12px;
    color: $black;

    @include grid-media($lg) {
      font-size: 25px;
    }


    em {
      float: left;
      display: inline-block;
      width: 100%;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 12px;
      margin-top: 10px;
      font-style: normal;
    }
  }
}

/*
Bullet Points

markup:
<div class="c-rte">
  <ul>
    <li>List item 1</li>
    <li>List item 2</li>
    <li>List item 3</li>
    <li>List item 4</li>
    <li>List item 5</li>
  </ul>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 1.4
*/

.c-rte, .c-rte__accordion {
  ul {
    li {
      position: relative;
      padding-left: 40px;
      line-height: 1;

      &:before {
        content: "";
        display: block;
        width: 15px;
        height: 10px;
        background: $maroon;
        position: absolute;
        left: 0;
        top: 5px;
      }

      &:after {
        content: "";
        display: block;
        margin-bottom: 25px;
      }
    }
  }
}

/*
Numbered List

markup:
<div class="c-rte">
  <ol>
    <li>List item 1</li>
    <li>List item 2</li>
    <li>List item 3</li>
    <li>List item 4</li>
    <li>List item 5</li>
  </ol>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 1.5
*/

.c-rte, .c-rte__accordion {
  ol {
    li {
      position: relative;
      padding-left: 40px;
      line-height: 1;
      counter-increment: table-ol;

      &:before {
        content: counter(table-ol);
        position: absolute;
        left: 0;
        color: $maroon;
        font-weight: bold;
      }

      &:after {
        content: "";
        display: block;
        margin-bottom: 25px;
      }
    }
  }
}