  <div class="heading">
    <h1>This is the heading for the video</h1>
  </div>
  <div class="video">
    <iframe width="640" height="360" src="https://www.youtube.com/embed/V8S591S-eU0?feature=oembed" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen=""></iframe>
  </div>
